import GetAuth from "./GetAuth"
import GetUserData from "./GetUserData"
import { setParentForm, showErrors } from "./HandleErrors"
import SetAuth from "./SetAuth"

const BASE_API = "http://127.0.0.1:8000/api"

const fetchData = (url: string, init: RequestInit): Promise<ApiT.ResponseT> => {
    return new Promise((resolve, reject) => {
        fetch(url, init)
            .then(async (res) => {
                const { ok, status, statusText } = res
                return {
                    ok,
                    status,
                    statusText,
                    data: await res.json()
                } as ApiT.ResponseT
            })
            .then((res) => (res.ok ? resolve(res) : reject(res)))
    })
}

const checkToken = async () => {
    const user = GetUserData()
    const exp = user.exp || 0

    const now = Math.floor(new Date().getTime() / 1000) + 10
    if (exp > now) return

    const { token_refresh } = GetAuth()
    if (!token_refresh) return

    const body = new FormData()
    body.append("token_refresh", token_refresh)

    try {
        const res = await fetchData(`${BASE_API}/refresh-token`, {
            method: "POST",
            body
        })
        SetAuth(res)
        return true
    } catch (e) {
        localStorage.clear()
        return false
    }
}

const handleRequest = async (path: string, init: RequestInit) => {
    try {
        await checkToken()
        const headers = init.headers
        init.headers = {
            ...headers,
            Authorization: `Bearer ${localStorage.getItem("token_access")}`
        }
        return await fetchData(`${BASE_API}/${path}`, init)
    } catch (e: any) {
        showErrors(e)
        return e as ApiT.ResponseT
    }
}

export const get = async (path: string) => {
    return await handleRequest(path, {
        method: "GET"
    })
}

export const post = async (path: string, body: HTMLFormElement) => {
    setParentForm(body)

    return await handleRequest(path, {
        method: "POST",
        body: new FormData(body)
    })
}

export const put = async (path: string, body: HTMLFormElement) => {
    setParentForm(body)

    const newBody = new FormData(body)
    newBody.append("_method", "PUT")

    return await handleRequest(path, {
        method: "POST",
        body: newBody
    })
}

export const destroy = async (path: string) => {
    return await handleRequest(path, {
        method: "DELETE"
    })
}

export const patch = async (path: string) => {
    return await handleRequest(path, {
        method: "PATCH"
    })
}
