import { createBrowserRouter } from "react-router-dom"
import LandingPage, { landingPageLoader } from "./Pages/LandingPage"
import { Suspense, lazy } from "react"
import { rootLoader } from "./Pages/Root"

const Root = lazy(() => import("./Pages/Root"))
const Dashboard = lazy(() => import("./Pages/Dashboard"))

const DataKeluarga = lazy(() => import("./Pages/DataKeluarga"))
const TambahAnggota = lazy(() => import("./Pages/DataKeluarga/TambahAnggota"))
const DetailKK = lazy(() => import("./Pages/DataKeluarga/DetailKK"))
const DetailAnggota = lazy(() => import("./Pages/DataKeluarga/DetailAnggota"))
const EditAnggota = lazy(() => import("./Pages/DataKeluarga/EditAnggota"))
const DataPendaftar = lazy(() => import("./Pages/DataPendaftar"))
const DataSurat = lazy(() => import("./Pages/DataSurat"))
const DetailSurat = lazy(() => import("./Pages/DataSurat/DetailSurat"))
const EditSurat = lazy(() => import("./Pages/DataSurat/EditSurat"))
const AjukanSurat = lazy(() => import("./Pages/AjukanSurat"))
const AddSurat = lazy(() => import("./Pages/AjukanSurat/Add"))
const ReadSurat = lazy(() => import("./Pages/AjukanSurat/ReadSurat"))
const KelolaAdmin = lazy(() => import("./Pages/KelolaAdmin"))
const SettingProfile = lazy(() => import("./Pages/SettingProfile"))

const FotoKK = lazy(() => import("./Pages/DataKeluarga/FotoKK"))
const SuratPengantar = lazy(() => import("./Pages/DataSurat/SuratPengantar"))

const router = createBrowserRouter([
    {
        path: "/",
        element: <LandingPage />,
        loader: landingPageLoader
    },
    {
        path: "/:type",
        element: (
            <Suspense>
                <Root />
            </Suspense>
        ),
        loader: rootLoader,
        children: [
            {
                index: true,
                element: (
                    <Suspense>
                        <Dashboard />
                    </Suspense>
                )
            },
            {
                path: "dashboard",
                index: true,
                element: (
                    <Suspense>
                        <Dashboard />
                    </Suspense>
                )
            },
            {
                path: "pendaftar",
                element: (
                    <Suspense>
                        <DataPendaftar />
                    </Suspense>
                )
            },
            {
                path: "data-keluarga",
                element: (
                    <Suspense>
                        <DataKeluarga />
                    </Suspense>
                )
            },
            {
                path: "data-keluarga/:no_kk",
                element: (
                    <Suspense>
                        <DetailKK />
                    </Suspense>
                )
            },
            {
                path: "data-keluarga/tambah",
                element: (
                    <Suspense>
                        <TambahAnggota />
                    </Suspense>
                )
            },
            {
                path: "data-keluarga/:no_kk/:nik",
                element: (
                    <Suspense>
                        <DetailAnggota />
                    </Suspense>
                )
            },
            {
                path: "data-keluarga/:no_kk/:nik/edit",
                element: (
                    <Suspense>
                        <EditAnggota />
                    </Suspense>
                )
            },
            {
                path: "ajukan-surat",
                element: (
                    <Suspense>
                        <AjukanSurat />
                    </Suspense>
                )
            },
            {
                path: "ajukan-surat/add",
                element: (
                    <Suspense>
                        <AddSurat />
                    </Suspense>
                )
            },
            {
                path: "data-surat",
                element: (
                    <Suspense>
                        <DataSurat />
                    </Suspense>
                )
            },
            {
                path: "data-surat/:jenis_surat/:id/detail",
                element: (
                    <Suspense>
                        <DetailSurat />
                    </Suspense>
                )
            },
            {
                path: "data-surat/:jenis_surat/:id/edit",
                element: (
                    <Suspense>
                        <EditSurat />
                    </Suspense>
                )
            },
            {
                path: "kelola-admin",
                element: (
                    <Suspense>
                        <KelolaAdmin />
                    </Suspense>
                )
            },
            {
                path: ":username",
                element: (
                    <Suspense>
                        <SettingProfile />
                    </Suspense>
                )
            }
        ]
    },
    {
        path: "/:type/ajukan-surat/cetak/:id",
        element: (
            <Suspense>
                <ReadSurat />
            </Suspense>
        )
    },
    {
        path: "/:type/data-keluarga/:no_kk/foto-kk",
        element: (
            <Suspense>
                <FotoKK />
            </Suspense>
        )
    },
    {
        path: "/:type/data-surat/:jenis_surat/:id/surat-pengantar",
        element: (
            <Suspense>
                <SuratPengantar />
            </Suspense>
        )
    }
])

export default router
