import FormGroup from "../../Components/FormGroup"
import Modal from "../../Components/Modal"
import { post } from "../../Functions/Api"
import { PopupAlert } from "../../Functions/Alerts"
import { el } from "../../Functions/GetElement"

const Register = () => {
    const register = async (evt: React.FormEvent<HTMLFormElement>) => {
        evt.preventDefault()

        const res = await post("register", evt.currentTarget)

        if (res.ok) {
            el(`[href="/#login-form"]`)?.click()

            PopupAlert.fire({
                icon: "success",
                text: "Berhasil Mendaftar"
            })
        }
    }

    const formGroupsRegisterL: FormGroupT[] = [
        {
            label: "Nomor KK",
            type: "number",
            name: "no_kk",
            id: "no_kk",
            placeholder: "1237217623363"
        },
        {
            label: "Password",
            type: "password",
            name: "password",
            id: "password_register",
            placeholder: "******"
        },
        {
            label: "Nama Kepala Keluarga",
            type: "nama",
            name: "nama",
            id: "nama",
            placeholder: "Budi Santoso"
        },
        {
            label: "NIK Kepala Keluarga",
            type: "number",
            name: "nik",
            id: "nik_register",
            placeholder: "72361361361"
        }
    ]

    const formGroupsRegisterR: FormGroupT[] = [
        {
            label: "Nomor Whatsapp",
            type: "number",
            name: "no_whatsapp",
            id: "no_whatsapp",
            placeholder: "08237623363"
        },
        {
            label: "Upload KK",
            type: "file",
            name: "foto_kk",
            id: "foto_kk"
        }
    ]

    return (
        <Modal idModal="register-form">
            <form onSubmit={register} className="row">
                <div className="col-md-6">
                    {formGroupsRegisterL.map((e, i) => (
                        <FormGroup {...e} key={i} />
                    ))}
                </div>
                <div className="col-md-6">
                    {formGroupsRegisterR.map((e, i) => (
                        <FormGroup {...e} key={i} />
                    ))}
                    <button
                        className="btn btn-primary ml-1"
                        style={{ width: "100%" }}
                    >
                        <i className="bx bx-check d-block d-sm-none"></i>
                        <span className="d-none d-sm-block">Buat Akun</span>
                    </button>
                </div>
            </form>
        </Modal>
    )
}

export default Register
