import { Link, useNavigate } from "react-router-dom"
import { post } from "../../Functions/Api"
import SetAuth from "../../Functions/SetAuth"
import GetAuth from "../../Functions/GetAuth"
import { el } from "../../Functions/GetElement"
import Modal from "../../Components/Modal"
import Form from "../../Components/Form"

const Login = () => {
    const navigate = useNavigate()

    const login = async (evt: React.FormEvent<HTMLFormElement>) => {
        evt.preventDefault()

        const res = await post("login", evt.currentTarget)
        if (!res.ok) return

        SetAuth(res)
        const { role } = GetAuth()
        let modal = el("#login-form .close")
        if (!modal) modal = el("#register-form .close")
        modal?.click()

        navigate(`/${role}/dashboard`)
    }

    const formGroupsLogin: FormGroupT[] = [
        {
            label: "Masukkan NIK/Username",
            type: "text",
            placeholder: "17710012973187371",
            id: "nik_login",
            name: "username"
        },
        {
            label: "Masukkan Password",
            type: "password",
            placeholder: "*****",
            id: "password_login",
            name: "password"
        }
    ]

    return (
        <Modal idModal="login-form" isLogin>
            <Form formGroup={formGroupsLogin} onSubmit={login}>
                <Link
                    to="/#forget-pw"
                    className="text-end my-3 text-decoration-underline"
                    data-bs-toggle="modal"
                    data-bs-dismiss="modal"
                >
                    Forget Password
                </Link>
                <div className="col-md-12">
                    <button className="btn btn-primary w-100">Login</button>
                </div>
            </Form>
        </Modal>
    )
}

export default Login
