const SetAuth = (res: ApiT.ResponseT) => {
    if (!res.ok) return

    localStorage.setItem("token_access", res.data.token_access)
    localStorage.setItem("token_refresh", res.data.token_refresh)
    if (!localStorage.getItem("role")) {
        localStorage.setItem("role", res.data.role)
    }
}

export default SetAuth
