import { Link } from "react-router-dom"
import Navbar from "../Components/Navbar"
import NavbarItem from "../Components/Navbar/NavbarItem"
import Icon from "../../Assets/images//logo/logo.png"

const Header = () => {
    return (
        <header>
            <nav className="navbar bg-white navbar-expand-lg bg-body-tertiary">
                <div className="container-fluid md p-0">
                    <Link className="navbar-brand " to="#">
                        <img
                            src={Icon}
                            alt="Logo"
                            style={{
                                width: "40px",
                                height: "40px",
                                marginRight: "1rem"
                            }}
                        />
                        Polandu
                    </Link>

                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#navbarSupportedContent"
                        aria-controls="navbarSupportedContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div
                        className="collapse navbar-collapse"
                        id="navbarSupportedContent"
                    >
                        <Navbar>
                            <NavbarItem to="#beranda" text="Beranda" />
                            <NavbarItem
                                to="#tentang-kami"
                                text="Tentang Kami"
                            />
                            <NavbarItem
                                to="#hubungi-kami"
                                text="Hubungi Kami"
                            />
                        </Navbar>
                        <div className="d-flex gap-2">
                            <button
                                type="button"
                                className="btn btn-outline-primary rounded-5 px-3"
                                data-bs-toggle="modal"
                                data-bs-target="#login-form"
                            >
                                Log In
                            </button>
                            <button
                                type="button"
                                className="btn btn-primary rounded-5 px-3"
                                data-bs-toggle="modal"
                                data-bs-target="#register-form"
                            >
                                Register
                            </button>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
    )
}

export default Header
