import FormGroup from "../../Components/FormGroup"

const ContactMe = () => {
    return (
        <section
            className="middle-content maps"
            style={{
                marginTop: "10rem"
            }}
            id="hubungi-kami"
        >
            <div className="card-content">
                <div className="card-body">
                    <div className="row">
                        <h2 className="card-title text-center mb-5">
                            Hubungi Kami
                        </h2>
                        <div className="col-md-6 col-sm-12">
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d15923.969664538357!2d102.27807468398903!3d-3.8117180282139285!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x2e36b070b1f9cd3d%3A0xb9826b5d76f31f82!2sJemb.%20Kecil%2C%20Kec.%20Singaran%20Pati%2C%20Kota%20Bengkulu%2C%20Bengkulu!5e0!3m2!1sid!2sid!4v1684901331283!5m2!1sid!2sid"
                                width="100%"
                                height="100%"
                                loading="lazy"
                                referrerPolicy="no-referrer-when-downgrade"
                                title="maps"
                            />
                        </div>
                        <div className="col-md-6 col-sm-12">
                            <form className="form" method="post">
                                <div className="form-body">
                                    <FormGroup
                                        label="Email"
                                        type="email"
                                        id="email"
                                        placeholder="jhon@gmail.com"
                                        name="email"
                                    />
                                    <FormGroup
                                        label="Nama Lengkap"
                                        type="text"
                                        id="nama_lengkap"
                                        placeholder="jhon buyung"
                                        name="nama"
                                    />
                                    <FormGroup
                                        label="Pesan"
                                        type="pesan"
                                        id="pesan"
                                        placeholder="Sampaikan pesan anda disini"
                                        name="pesan"
                                    />
                                </div>
                                <div className="form-actions d-flex justify-content-end">
                                    <button
                                        type="submit"
                                        className="btn btn-primary me-1"
                                    >
                                        Kirim Email
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ContactMe
