const useForm = () => {
    const create = (children: UseFormChild[]) => {
        const form = document.createElement("form")
        children.map(({ name, value }) => {
            const child = document.createElement("input")
            child.name = name
            child.value = value
            return form.append(child)
        })

        return form
    }

    return create
}

export default useForm
