import Card from "../Components/Card"

const AboutMe = () => {
    return (
        <section
            className="middle-content"
            style={{
                marginTop: "5rem"
            }}
            id="tentang-kami"
        >
            <div className="row">
                <div className="header col-12 d-flex justify-content-center">
                    <h2 className="section-title">Tentang Polandu JK</h2>
                </div>
            </div>
            <div className="row mt-4">
                <div className="content col-10 text-center mx-auto">
                    <p>
                        Polandu JK adalah website untuk masyarakat Kelurahan
                        Jembatan Kecil yang memungkinkan pengajuan surat-surat
                        secara daring, seperti surat keterangan tidak mampu dan
                        surat pengantar perkawinan. Dengan Polandu JK, pengguna
                        dapat mengurus surat secara online dan mempercepat
                        proses pengambilannya. Tujuannya adalah mempermudah
                        akses layanan administrasi publik dan meningkatkan
                        efisiensi pelayanan.
                    </p>
                </div>
            </div>
            <div
                className="row text-white pt-5"
                style={{
                    height: "200px"
                }}
            >
                <Card md={4} sm={12}>
                    <div className="card-body text-center">
                        <i className="bi bi-file-plus fs-1" />
                        <p className="card-text">
                            Kemudahan Pengajuan Surat Secara Daring
                        </p>
                    </div>
                </Card>
                <Card md={4} sm={12}>
                    <div className="card-body text-center">
                        <i className="bi bi-calendar fs-1" />
                        <p className="card-text">
                            Pengurangan Waktu dan Tenaga
                        </p>
                    </div>
                </Card>
                <Card md={4} sm={12}>
                    <div className="card-body text-center">
                        <i className="bi bi-mailbox fs-1" />
                        <p className="card-text">Efisiensi Layanan Publik</p>
                    </div>
                </Card>
            </div>
        </section>
    )
}

export default AboutMe
