import SidebarItem from "./Sidebar/SidebarItem"
import Icon from "../../Assets/images/logo/logo.png"
import { Link, useLocation, useParams } from "react-router-dom"
import { useCallback, useEffect } from "react"
import { el } from "../../Functions/GetElement"

const Sidebar = () => {
    const { pathname } = useLocation()
    const { type } = useParams()

    const active = useCallback(() => {
        el(".menu .active")?.classList.remove("active")

        let element = el(`#sidebar .menu [href="${pathname}"]`)
        if (element) localStorage.setItem("current_active_sidebar", pathname)

        if (!element) {
            element = el(
                `#sidebar .menu [href="${localStorage.getItem(
                    "current_active_sidebar"
                )}"]`
            )
        }
        element?.parentElement?.classList.add("active")
    }, [pathname])

    useEffect(() => {
        active()
    }, [active])

    const sidebarItems: SidebarT.SidebarItemT[] = [
        {
            classIcon: "bi-grid-fill",
            text: "Dashboard",
            to: `/${type}/dashboard`
        },
        {
            classIcon: "bi-people-fill",
            text: "Pendaftar",
            to: `/${type}/pendaftar`,
            role: "admin"
        },
        {
            classIcon: "bi-credit-card-2-front-fill",
            text: "Data Keluarga",
            to: `/${type}/data-keluarga`
        },
        {
            classIcon: "bi-file-earmark-fill",
            text: "Ajukan Surat",
            to: `/${type}/ajukan-surat`,
            role: "user"
        },
        {
            classIcon: "bi-file-earmark-fill",
            text: "Data Surat",
            to: `/${type}/data-surat`,
            role: "admin"
        },
        {
            classIcon: "bi-headset",
            text: "Kelola Admin",
            to: `/${type}/kelola-admin`,
            role: "super_admin"
        },
        {
            classIcon: "bi-person-fill",
            text: "Ubah Profil",
            to: `/${type}/ubah-profil`
        }
    ]

    return (
        <div id="sidebar" className="active">
            <div className="sidebar-wrapper active d-flex flex-column bg-bgcolor">
                <div className="sidebar-header">
                    <div className="d-flex justify-content-between">
                        <div className="logo">
                            <Link className="fs-5" to={`/${type}/dashboard`}>
                                <img
                                    src={Icon}
                                    alt="Logo"
                                    style={{ width: "30px", height: "30px" }}
                                />{" "}
                                Polandu JK
                            </Link>
                        </div>
                        <div className="toggler">
                            <Link
                                to="#"
                                className="sidebar-hide d-xl-none d-block"
                            >
                                <i className="bi bi-x bi-middle"></i>
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="sidebar-menu d-flex flex-column">
                    <ul className="menu" onClick={active}>
                        <li className="sidebar-title"></li>
                        {sidebarItems.map((e, i) => {
                            if (
                                !e.role ||
                                e.role === type ||
                                (type === "super_admin" && e.role === "admin")
                            ) {
                                return <SidebarItem {...e} key={i} />
                            }

                            return ""
                        })}
                    </ul>

                    <ul className="menu">
                        <SidebarItem
                            classIcon="bi-x-square-fill text-danger"
                            text="Keluar"
                            to="/"
                            color="danger"
                            onClick={() => localStorage.clear()}
                        />
                    </ul>
                </div>
                <button className="sidebar-toggler btn x">
                    <i data-feather="x"></i>
                </button>
            </div>
        </div>
    )
}

export default Sidebar
