import { memo } from "react"

const Card = ({ children, lg, md, sm }: PagesComponentsT.CardT) => {
    return (
        <div
            className={`h-100 ${lg ? `col-lg-${lg}` : ""} ${
                md ? `col-md-${md}` : ""
            } ${sm ? `col-sm-${sm}` : ""}`}
        >
            <div className="card bg-primary h-100">
                <div className="card-content">{children}</div>
            </div>
        </div>
    )
}

export default memo(Card)
