import fotoAplikasi from "../../Assets/images/LandingPage/foto-aplikasi.png"

const HomePage = () => {
    return (
        <section className="row top-content mt-5" id="beranda">
            <div className="col-12">
                <div className="row">
                    <div className="col-8 pt-4">
                        <h4 className="fs-2">
                            Ajukan Surat, Hanya Dengan Sentuhan Jari dengan
                            Polandu JK
                        </h4>
                        <p>
                            Polandu JK (Pos Pelayanan Terpadu Jembatan Kecil)
                            adalah sebuah website untuk mempermudah masyarakat
                            Kelurahan Jembatan Kecil dalam pengajuan surat-surat
                            secara daring seperti surat keterangan tidak mampu,
                            surat pengantar perkawinan, surat keterangan usaha
                            dan surat lainnya.
                        </p>
                        <div className="d-flex">
                            <button
                                className="btn btn-primary"
                                type="button"
                                data-bs-toggle="modal"
                                data-bs-target="#login-form"
                            >
                                Ajukan Sekarang
                            </button>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="card ps-5">
                            <div className="bg-image">
                                <img
                                    src={fotoAplikasi}
                                    alt="Foto Aplikasi"
                                    style={{
                                        height: "300px"
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default HomePage
