import { useState } from "react"
import Icon from "../../Assets/images/logo/logo.png"
import FormGroup from "../../Components/FormGroup"
import { PopupAlert } from "../../Functions/Alerts"
import { post } from "../../Functions/Api"
import useForm from "../../Hooks/useForm"
import { el } from "../../Functions/GetElement"

const ForgetPassword = () => {
    const [showOtp, setShowOtp] = useState<boolean>(false)
    const formBuilder = useForm()

    const getOtp: React.FormEventHandler<HTMLFormElement> = async (evt) => {
        evt.preventDefault()

        const res = await post("forget-password", evt.currentTarget)
        console.log(res)
        if (!res.ok) return

        setShowOtp((curr) => !curr)
        localStorage.setItem(
            "nik_kepala_keluarga",
            res.data.nik_kepala_keluarga
        )
    }

    const sendOtp: React.FormEventHandler<HTMLFormElement> = async (evt) => {
        evt.preventDefault()

        const otp = el<HTMLInputElement>(`input[name="otp"]`)
        const form = formBuilder([
            { name: "otp", value: otp.value },
            {
                name: "username",
                value: localStorage.getItem("nik_kepala_keluarga") || ""
            }
        ])

        const res = await post("reset", form)
        if (!res.ok) return

        el<HTMLButtonElement>("#forget-pw .close").click()
        el<HTMLButtonElement>(`[data-bs-target="#login-form"]`).click()

        PopupAlert.fire({
            icon: "success",
            text: "Akun anda berhasil direset, silahkan login!"
        })
    }

    return (
        <div
            className={`modal fade text-left`}
            id="forget-pw"
            tabIndex={-1}
            role="dialog"
            aria-hidden="true"
        >
            <div
                className="modal-dialog modal-dialog-centered modal-dialog-scrollable"
                role="document"
            >
                <div className="modal-content">
                    <div className="modal-header">
                        <div className={`w-100 text-center`}>
                            <img
                                src={Icon}
                                alt="Logo"
                                style={{
                                    width: "54px",
                                    height: "54px"
                                }}
                            />
                            <h3 className="fs-5 text-dark fw-bold mt-3">
                                POLANDU JK
                            </h3>
                        </div>
                        <button
                            type="button"
                            className="close d-none"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        >
                            <i data-feather="x"></i>
                        </button>
                    </div>
                    <div className="modal-body">
                        {!showOtp ? (
                            <form onSubmit={getOtp}>
                                <FormGroup
                                    label="Nomor Telepon"
                                    name="phone_number"
                                />
                                <FormGroup
                                    label="Nik Kepala Keluarga"
                                    name="nik_kepala_keluarga"
                                />

                                <div className="d-flex justify-content-end">
                                    <button className="btn btn-primary">
                                        Minta Kode OTP
                                    </button>
                                </div>
                            </form>
                        ) : (
                            <form onSubmit={sendOtp}>
                                <FormGroup
                                    label="Masukkan Kode OTP"
                                    name="otp"
                                />

                                <p
                                    className="alert alert-warning text-center my-3"
                                    role="alert"
                                >
                                    Kode OTP dikirimkan ke nomor whatsapp Anda!
                                </p>

                                <div className="d-flex justify-content-end">
                                    <button className="btn btn-primary">
                                        Reset Password
                                    </button>
                                </div>
                            </form>
                        )}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ForgetPassword
