import { memo, useCallback, useEffect } from "react"
import { el } from "../../Functions/GetElement"
import { useLocation } from "react-router-dom"

const Navbar = ({ children }: NavbarT.NavbarT) => {
    const { hash } = useLocation()
    const active = useCallback(() => {
        const target = hash || "#beranda"

        el(".navbar-nav .active")?.classList.remove("active")
        el(`.nav-link[href="${target}"]`)?.classList.add("active")
    }, [hash])

    useEffect(() => {
        active()
    }, [active])

    return (
        <ul className="navbar-nav me-auto mb-2 mb-lg-0 mx-auto">{children}</ul>
    )
}

export default memo(Navbar)
