import { memo } from "react"
import Icon from "../Assets/images/logo/logo.png"
import { Link } from "react-router-dom"

const Modal = ({ children, idModal, isLogin }: ModalT) => {
    return (
        <div
            className={`modal fade text-left ${isLogin ? "" : "modal-lg"}`}
            id={idModal}
            tabIndex={-1}
            role="dialog"
            aria-labelledby={idModal.toUpperCase()}
            aria-hidden="true"
        >
            <div
                className="modal-dialog modal-dialog-centered modal-dialog-scrollable"
                role="document"
            >
                <div className="modal-content">
                    <div className="modal-header">
                        <div
                            className={`w-100 ${
                                isLogin
                                    ? "text-center"
                                    : "d-flex align-items-center gap-3 py-4"
                            }`}
                        >
                            <img
                                src={Icon}
                                alt="Logo"
                                style={{
                                    width: "54px",
                                    height: "54px"
                                }}
                            />
                            <h3 className="fs-5 text-dark fw-bold mt-3">
                                POLANDU JK
                            </h3>
                        </div>
                        <button
                            type="button"
                            className="close d-none"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        >
                            <i data-feather="x"></i>
                        </button>
                    </div>
                    <div className="modal-body">{children}</div>
                    <div className="modal-footer">
                        <p className="col-md-12 mt-3 text-center">
                            {isLogin ? "Belum " : "Sudah "}
                            memiliki akun?
                            <Link
                                data-bs-dismiss="modal"
                                data-bs-toggle="modal"
                                to={
                                    isLogin ? "/#register-form" : "/#login-form"
                                }
                            >
                                {isLogin ? " Register" : " Login"} Sekarang
                            </Link>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default memo(Modal)
