import Swal, { SweetAlertOptions, SweetAlertResult } from "sweetalert2"

export const PopupAlert = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer)
        toast.addEventListener("mouseleave", Swal.resumeTimer)
    }
})

export const ConfirmAlert = (
    options: SweetAlertOptions,
    action: () => void
) => {
    Swal.fire({
        showCancelButton: true,
        confirmButtonColor: "#1E2566",
        cancelButtonColor: "#d33",
        cancelButtonText: "Batal",
        ...options
    }).then((result) => {
        if (result.isConfirmed) action()
    })
}

export const ConfirmDelete = (
    action: () => void,
    options?: SweetAlertOptions
) => {
    ConfirmAlert(
        {
            title: "Apakah Anda Yakin?",
            text: "Kamu tidak bisa membatalkan ini!",
            cancelButtonText: "Batal",
            icon: "warning",
            ...options
        },
        action
    )
}

export const UploadFileAlert = (action: (form: HTMLFormElement) => void) => {
    Swal.fire({
        showCancelButton: true,
        title: "Setujui?",
        html: /* html */ `<p>Apakah Anda Menyetujui Pembuatan Surat Ini?</p>
            <form id="form-file">
            <label for="formFile" class="form-label">Upload surat yang sudah disetujui</label>
            <input class="form-control" type="file" name="file_surat" id="formFile" />
            </form>`,
        confirmButtonText: "Setujui",
        confirmButtonColor: "#1E2566",
        cancelButtonText: "Batal",
        cancelButtonColor: "#D2D4D6",
        icon: "question",
        preConfirm: () => {
            const input = document.querySelector(
                "#formFile"
            ) as HTMLInputElement
            return !input.value
                ? Swal.showValidationMessage("Pilih file terlebih dahulu")
                : true
        }
    }).then((result) => {
        if (!result.isConfirmed) return
        const form = document.querySelector("#form-file") as HTMLFormElement
        action(form)
        console.log(form)
    })
}
