import { redirect } from "react-router-dom"
import AboutMe from "./LandingPage/AboutMe"
import ContactMe from "./LandingPage/ContactMe"
import Footer from "./LandingPage/Footer"
import Header from "./LandingPage/Header"
import HomePage from "./LandingPage/HomePage"
import Login from "./LandingPage/Login"
import Register from "./LandingPage/Register"
import GetAuth from "../Functions/GetAuth"
import ForgetPassword from "./LandingPage/ForgetPassword"

export const landingPageLoader = () => {
    const { role, token_access, token_refresh } = GetAuth()
    return token_access && token_refresh ? redirect(`/${role}/dashboard`) : null
}

const LandingPage = () => {
    return (
        <>
            <Header />
            <div className="page-content">
                <HomePage />
                <AboutMe />
                <ContactMe />
            </div>
            <Footer />
            <Login />
            <Register />
            <ForgetPassword />
        </>
    )
}

export default LandingPage
