import { Link, LoaderFunctionArgs, Outlet, redirect } from "react-router-dom"
import Sidebar from "./Components/Sidebar"
import GetAuth from "../Functions/GetAuth"
import { createContext } from "react"
import useUserData from "../Hooks/useUserData"
import { useAuth } from "../Hooks/useAuth"

export const rootLoader = ({ params }: LoaderFunctionArgs) => {
    const { type } = params
    const { token_access, token_refresh, role } = GetAuth()

    if (!role || role === "undefined" || !token_access || !token_refresh) {
        localStorage.clear()
        return redirect("/")
    }
    return role !== type ? redirect(`/${role}/dashboard`) : null
}

export const RootContext = createContext<Root.UserContextT>({
    auth: {},
    user: {}
})

const Root = () => {
    const user = useUserData()
    const auth = useAuth()

    return (
        <RootContext.Provider value={{ auth, user }}>
            <Sidebar />
            <div id="main">
                <header className="mb-3">
                    <Link to="#" className="burger-btn d-block d-xl-none">
                        <i className="bi bi-justify fs-3"></i>
                    </Link>
                </header>
                <Outlet />
            </div>
        </RootContext.Provider>
    )
}

export default Root
