import { PopupAlert } from "./Alerts"
import { el } from "./GetElement"

let parentForm: undefined | HTMLFormElement

export const setParentForm = (form: HTMLFormElement) => {
    parentForm = form
}

export const showErrorMessage = (message: string) => {
    PopupAlert.fire({
        icon: "warning",
        text: message
    })
}

export const showErrorErrors = (errors: Record<string, any>) => {
    for (const error in errors) {
        const element =
            parentForm?.querySelector(`[name="${error}"]`) ||
            el(`[name="${error}"]`)
        element?.classList.add("is-invalid")

        if (!element) return
        const currentInvalid =
            element.parentElement?.querySelector(".invalid-feedback")
        if (currentInvalid) currentInvalid.textContent = errors[error][0]

        if (!currentInvalid) {
            const message = document.createElement("p")
            message.classList.add("invalid-feedback")
            message.textContent = errors[error][0]
            element.parentElement?.append(message)
        }

        element?.addEventListener("input", (evt: Event) => {
            const target = evt.target as HTMLInputElement
            target.classList.remove("is-invalid")
        })
    }
}

export const showErrors = (res: ApiT.ResponseT) => {
    if (res.status === 400) showErrorErrors(res.data)
    if ("message" in res.data) showErrorMessage(res.data.message)
}
