import { memo } from "react"
import FormGroup from "./FormGroup"

const Form = ({ formGroup, onSubmit, children }: FormT) => {
    return (
        <form onSubmit={onSubmit}>
            <div className="row">
                {formGroup.map((e, i) => (
                    <FormGroup {...e} key={i} />
                ))}
                {children}
            </div>
        </form>
    )
}

export default memo(Form)
